<script>
import ColorSelector from "../../components/V3ColorPicker/V3ColorPicker.vue"
export default {
  name: "LabelMarkersConfig",
  components: {
    ColorSelector
  },
  props: {
    model: {
      type: [Object],
      required: true
    }
  },
  data () {
    return {
      labelSpan: 6,
      wrapperSpan: 18,
      anchorOptions: [
        { label: '', value: 'top-left' },
        { label: '', value: 'middle-left' },
        { label: '', value: 'bottom-left' },
        { label: '', value: 'top-center' },
        { label: '', value: 'center' },
        { label: '', value: 'bottom-center' },
        { label: '', value: 'top-right' },
        { label: '', value: 'middle-right' },
        { label: '', value: 'bottom-right' }
      ],
      directionOptions: [
        { label: '', value: 'top' },
        { label: '', value: 'right' },
        { label: '', value: 'bottom' },
        { label: '', value: 'left' },
        { label: '', value: 'center' }
      ]
    }
  },
  methods: {
    add () {
      const item = {
        visible: true,
        position: this.model.map.center,
        zIndex: 12,
        zooms: [2, 20],
        icon: {
          image: 'https://a.amap.com/jsapi_demos/static/images/poi-marker.png',
          anchor: 'bottom-center',
          size: [25, 34],
          clipOrigin: [459, 92],
          clipSize: [50, 68]
        },
        text: {
          content: '',
          direction: 'right',
          offset: [0, 0],
          style: {
            fontSize: 12,
            fillColor: '#000000',
            strokeColor: '#ffffff',
            padding: [3, 3, 3, 3],
            backgroundColor: 'rgba(255,255,255,0)',
            borderColor: 'rgba(255,255,255,0)',
            borderWidth: 0,
            fold: false
          }
        }
      }
      this.model.labelMarkers.push(item)
    },
    remove (index) {
      this.model.labelMarkers.splice(index, 1)
    },
  },
}
</script>

